<template>
    <div class="right">
        <div class="header">
          <i v-if="isCollapse==false"  class="iconfont icon-right-indent" @click="change()"></i>
          <i v-else  class="iconfont icon-left-indent" @click="change()"></i>
          <!-- <div class="person" @click="drawer = true">陈天</div> -->
          <el-drawer
  title="我的"
  :visible.sync="drawer"
  :direction='direction'
  :before-close="handleClose">
   <ul>
    <li v-for="(i,index) of 6">{{i}}</li>
   </ul>
   <el-button @click="ppt()">按钮</el-button>
</el-drawer>
          <div class="headerright">
            <div>
              <el-dropdown @command="changeL">
  <span class="el-dropdown-link">
    切换语言<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
  <el-dropdown-menu slot="dropdown">
    <el-dropdown-item command="zh">简体中文</el-dropdown-item>
    <el-dropdown-item command="en">English</el-dropdown-item>
  </el-dropdown-menu>
</el-dropdown>
            </div>
            <div class="user" @click="drawer = true" style="cursor: pointer;">
              欢迎：
              <!-- <a  @click="drawer = true">{{userinfo.user}}</a> -->
              {{userinfo.user}}
              <!-- <span class="el-dropdown-link">
              <i class="iconfont icon-tuichu" @click="loginout" style="font-size:60px;"></i>
             </span> -->
              <!-- <el-button @click="loginout">退出登录</el-button> -->
              <!-- <span @click="loginout">退出登录</span> -->
            </div>
            <span class="el-dropdown-link">
              <i class="iconfont icon-tuichu" @click="loginout" style="font-size:30px;margin-left: 20px;"></i>
             </span>
          </div>
        </div>
    <div class="d">
      <router-view/>
    </div>
    </div>
  </template>
  
  <script>
  import {mapState,mapMutations} from 'vuex'
  
  export default {
    props:['isCollapse'],
    data(){
      return{
        drawer: false,
        direction: 'ltr',
      }
    },
    computed:{
      ...mapState('loginmodule',['userinfo'])
    },
    methods:{
      ppt(){
        alert('HHHHH')
      },
      change(){
        this.$emit('change',this.isCollapse)
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      changeL(command){
        this.$i18n.locale=command;
      },
      ...mapMutations('loginmodule',['clearUser']),
      loginout(){
        console.log('退出登录');
        this.clearUser();
        // localStorage.removeItem('user');
        sessionStorage.removeItem('user');
        this.$router.push('/login')
      }
    }
}
  </script>
  
  <style lang="less" scoped>
  li{
    height: 30px;
    width: auto;
    background-color: aqua;
    border: 0.5px solid white;
    text-align: center;
    line-height: 30px;
  }
    .icon-right-indent,.icon-left-indent{
      font-size: 30px;
      line-height: 60px;
    }
    .el-dropdown-link {
    cursor: pointer;
    color: white;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
  .header{
    width: 100%;
    height: 60px;
    background-color: aquamarine;
}
.person{
  line-height: 60px;
  display: flex;
  color:white;
  float: right;
}
.headerright{
  line-height: 60px;
  display: flex;
  color:white;
  float: right;
  padding-right: 20px;
  .user{
    margin-left: 20px;
    line-height: 60px;
  }
}

  </style>
  