import axios from "axios";
import base from "./base";
//node>js
// const qs=require('querystring')
import qs from 'qs'
const api={
    getshangplist(params){//page
        return axios.get(base.shangp,{
            params
        })
    },
    getsearch(params){
        return axios.get(base.search,{
            params
        })
    },
    addlist(params){
        return axios.get(base.add,{
            params
        })
    },
    deletelist(params){
        return axios.get(base.delete,{
            params
        })
    },
    updatelist(params){
        return axios.get(base.update,{
            params
        })
    },
    getlll(params){
        return axios.get(base.lll,{
            params
        })
    },
    getech(params){
        return axios.get(base.ech,{
            params
        })
    },
    getlogin(params){//username pass
        console.log("---",params,qs.stringify(params));
        return axios.post(base.login,qs.stringify(params))
    }
   
}
export default api;