<template>
    <div>
        <div class="login">
          <h2>登录</h2>
          <div>
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  
              <el-form-item label="用户名" prop="username">
    <el-input type="text" v-model="ruleForm.username" autocomplete="off"></el-input>
  </el-form-item>

        <el-form-item label="密码" prop="pass">
    <el-input type="password" v-model="ruleForm.pass" autocomplete="off" show-password></el-input>
  </el-form-item>
  <el-form-item label="验证码" prop="mode">
    <el-input type="text" v-model="ruleForm.mode" autocomplete="off"></el-input>
  </el-form-item>
  <div id="wwe" style="text-align:center ;">
    {{mm}}
  </div>
  <!-- <el-form-item label="确认密码" prop="checkPass">
    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
  </el-form-item> -->
  <!-- <div style="color:red;text-align: center;font-size: 12px;">{{info}}</div> -->
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
          </div>
        </div>
        <div style="position: fixed;bottom:20px;  right: 20px; color: red;">
          <h4>由于没买服务器不能与数据交互,登录改为vueX登录</h4>
          <h4>username:admin</h4>
          <h4>password:123456</h4>
        </div>
    </div>
  </template>
  
  <script>
import {mapMutations} from 'vuex'
import { mapState } from 'vuex';
  
  export default {
    computed:{
      ...mapState('users',['user'])
    },
    mounted(){
      this.piw()
    },
    data() {
      var validateUser = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名'));
        } else {
          callback();
        }
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          // if (this.ruleForm.checkPass !== '') {
          //   this.$refs.ruleForm.validateField('checkPass');
          // }
          callback();
        }
      };
      var validateYan=(rule,value,callback)=>{
        if(value===''){
          callback(new Error('请输入验证码'));
        }else{
          callback();
        }
      }
      // var validatePass2 = (rule, value, callback) => {
      //   if (value === '') {
      //     callback(new Error('请再次输入密码'));
      //   } else if (value !== this.ruleForm.pass) {
      //     callback(new Error('两次输入密码不一致!'));
      //   } else {
      //     callback();
      //   }
      // };
      return {
        // info:'',
        mm:'',
        ruleForm: {
          pass: '',
          // checkPass: '',
          username: '',
          mode:''
        },
        rules: {
          username:[
          {validator: validateUser, trigger: 'blur'}
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          mode: [
            { validator: validateYan, trigger: 'blur' }
          ],
          // checkPass: [
          //   { validator: validatePass2, trigger: 'blur' }
          // ],
        }
      };
    },
    methods: {
      ...mapMutations('loginmodule',['setUser']),
      
      piw(){
        var e = 6;
			  var t = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
			  var a = t.length;
			  var n = "";
			  for (var i = 0; i < e; i++){
          n += t.charAt(Math.floor(Math.random() * a));
        } 
        this.mm=n
        console.log(this.mm);
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // alert('submit!');
              if(this.ruleForm.username==this.user.username && this.ruleForm.pass==this.user.pass && this.ruleForm.mode==this.mm){
                let obj={
                  user:this.ruleForm.username,
                  token:true
                }
                this.setUser(obj)
                // localStorage.setItem('user',JSON.stringify(obj))
                sessionStorage.setItem('user',JSON.stringify(obj))
                //跳转
                this.$router.push('/')
                // this.info='';
                this.$message({
                  showClose: true,
                      message: '恭喜你,登录成功',
                      type: 'success'
                  });
              }else{
                // this.info='账号或密码错误！'
                if(this.ruleForm.mode!=this.mm){
                  this.piw();
                this.$message({
                  showClose: true,
                  message: '抱歉，验证码错误',
                  type: 'error'
                });
                }else{
                  console.log('错误');
                this.piw();
                this.$message({
                  showClose: true,
                  message: '抱歉，账号或密码错误',
                  type: 'error'
                });
                }
                
              }
              

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  
  }
  </script>
  
  <style lang="less" scoped>
    .login{
      width: 400px;
      height: 300px;
      margin: 100px auto;
      border-radius: 10px;
      background-color: white;
      border: 1px solid greenyellow;
      padding: 20px;
    }
    #wwe{
      width: 80px;
      height: 25px;
      background-color: aqua;
    }
  </style>
  