        <Righttop :isCollapse="isCollapse"  @change="changd()" class="right" :class="{op:isCollapse}" />
    </div>
  </template>
  
  <script>
    import Daoh from '../left/daoh.vue';
    import Righttop from '../righttop/righttop.vue';
  export default {
    components: { Daoh,
        Righttop
     },
     data(){
      return{
        isCollapse:false,
        nod:true,
      }
     },
     methods:{
        changd(){
          this.isCollapse=!this.isCollapse,
          this.nod=!this.nod
        }
    }
}
  </script>
  
  <style lang="less" scoped>
  .buju{
    display: flex;
    .daoh{
        width: 210px;
        height: 100%;
        background-color: #545c64;
        position: fixed;
        transition: all 0.3s linear;
        

    }
    .ap{
      width: 64px;
      transition: all 1s linear;
    }
    .right{
      flex: 1;
      margin-left: 210px ;
      width: 200px;
      transition: all 0.3s linear;
      
    }
    .op{
      margin-left: 64px;
      transition: all 1s linear;

    }
   
  }
  
  </style>
  