import Vue from 'vue'
import VueRouter from 'vue-router'
// import { component } from 'vue/types/umd'

import buju from '../views/buju/buju.vue'
import zhuy from '../views/right/zhuy.vue'
import login from '../views/login/login.vue'
//异步
const shangp=()=>import('../views/right/shangp.vue')
const dingd=()=>import('../views/right/dingd.vue')
//子级路由
const addshangp=()=>import('../views/addshangp/addshangp.vue')
const update=()=>import('../components/update/update.vue')
const guig=()=>import('../views/right/jiag.vue')
const backorder=()=>import('../views/right/backorder.vue')
const hetong=()=>import('../views/right/hetong.vue')
Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    name:'buju',
    component:buju,
    //路由元信息
    meta:{
      isLogin:true
    },
    children:[
      {
        path:'/hetong',
        name:'hetong',
        component:hetong,
      },
      {
        path:'/backorder',
        name:'backorder',
        component:backorder,
      },
      {
        path:'/guig',
        name:'guig',
        component:guig,
      },
      {
        path:'/dingd',
        name:'dingd',
        component:dingd,
      },
      {
        path:'/update',
        name:'update',
        component:update,
      },
      {
        path:'/',
        name:'zhuy',
        component:zhuy,
      },
      {
        path:'/shangp',
        name:'shangp',
        component:shangp
      },
      {
        path:'/addshangp',
        name:'addshangp',
        component:addshangp
      }
    ]
  },
  {
    path:'/login',
    name:'login',
    component:login,
  }
]

const router = new VueRouter({
  mode:'history',
  routes
})

// router.afterEach((to,from)=>{
//   document.title='XXX后台管理系统';
// })

import store from '../store/index'

router.beforeEach((to,from,next)=>{
  console.log('-----to-----',to);
  //判断是否要登录
  if(to.matched.some(ele=>ele.meta.isLogin)){
    //判断用户是否以及登录
    let token=store.state.loginmodule.userinfo.token;
    if(token){
      next();
    }else{
      next('/login')
    }
  }else{
    next();
  }
  
})

export default router
